@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,700;&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fraunces:ital,opsz,wght@0,9..144,100..900;1,9..144,100..900&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    margin-block: 0;
    margin-inline: 0;
}

body {
    height: 100vh;
    width: 100vw;
    background-color: hsl(30, 38%, 92%);
    display: flex;
    justify-content: center;
    align-items: center;
}

main {
    display: flex;
    width: 600px;
    margin-top: 102px;
    height: 447px;

    &>* {
        width: 100%;
    }

}

figure {

    & img {
        width: 100%;
        height: 100%;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
    }
}

article {
    background-color: hsl(0, 0%, 100%);
    display: flex;
    flex-direction: column;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;


    & p1 {
        
        color: hsl(228, 12%, 48%);
        margin-bottom: 2px;
        margin-top: 32.5px;
        margin-left: 34px;
        font-size: 12px;
        letter-spacing: 5.4px;
        font-family: 'Montserrat', sans-serif;
    }

    & p2 {
        font-family: 'Montserrat', sans-serif;
        color: hsl(228, 12%, 48%);
        margin-bottom: 0px;
        margin-left: 31.5px;
        margin-top: 16.5px;
        font-size: 14px;
        max-width: 230px;
        letter-spacing: 1px;
        line-height: 21.3px;
    }

    & h1 {
        font-family: "Fraunces";
        color: hsl(212, 21%, 14%);
        font-size: 32px;
        margin-top: 19px;
        margin-bottom: 13px;
        margin-left: 32.2px;
        line-height: 32.3px;
    }


}


section2 {
    margin-left: 34px;

    & p2 {
        color: hsl(228, 12%, 48%);
        font-size: 12.8px;
        text-decoration: line-through;
        font-family: 'Fraunces';
        margin-top: 2px;
        margin-right: 54px;
    }

    & h2 {
       
        font-family: "Fraunces";
        color: hsl(158, 36%, 37%);
        font-size: 32px;
        margin-top: 25.7px;
        margin-bottom: 25px;
    }

    & #preco {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

}


button {
    
    width: 236px;
    height: 47.5px;
    background-color: hsl(158, 36%, 37%);
    border: none;
    border-radius: 8px;
    margin-top: 1px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 33px;

    & img {
        margin-left: 56.8px;

    }

    & p3 {
        color: hsl(0, 0%, 100%);
        font-size: 16px;
        margin-left: 12px;
        font-weight: 500;
        text-align: center;

    }
}